import styled from "styled-components";
import { MdKeyboardArrowRight, MdArrowForward } from "react-icons/md";
import COL from "../Colors";

export const HeroContainer = styled.div`
  background: ${COL.heroBackgroundColor};
  display: flex;
  justify-content: center;
  align-items: center;
  // padding 0 30px;
  height: 800px;
  position: relative;
  z-index: 1;

  :before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.2) 0%,
        rgba(0, 0, 0, 0.6) 100%
      ),
      linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, transparent 100%);
    z-index: 2;
  }
`;

export const HeroBg = styled.div`
  position: relative;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
`;

export const VideoBg = styled.video`
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  background: #232a34;
`;

export const HeroContent = styled.div`
  z-index: 3;
  max-width: 1200px;
  position: absolute;
  padding: 8px 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

export const HeroH1 = styled.h1`
  color: ${COL.heroTitleColor};
  font-size: 48px;
  text-align: center;

  @media screen and (max-width: 768px) {
    margin-top: 75px;
    font-size: 30px;
  }

  @media screen and (max-width: 480px) {
    font-size: 16px;
    margin-top: 50px;
  }
`;

export const HeroP = styled.p`
  color: ${COL.heroTextColor};
  font-size: 24px;
  text-align: center;
  margin-top: 15px;
  max-width: 600px;

  @media screen and (max-width: 768px) {
    font-size: 20px;
    margin-top: 5px;
  }

  @media screen and (max-width: 480px) {
    font-size: 18px;
    margin-top: 0;
  }
`;

export const HeroBtnWrapper = styled.div`
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

export const ArrowForward = styled(MdArrowForward)`
  margin-left: 8px;
  font-size: 20px;
`;

export const ArrowRight = styled(MdKeyboardArrowRight)`
  margin-left: 8px;
  font-size: 20px;
`;

export const RlogoWrap = styled.img`
  width: 75px;
  height: 75px;
  margin-right: -30px;

  @media screen and (max-width: 480px) {
    width: 25px;
    height: 25px;
    margin-right: 0;
  }
`;
